@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap");
.chronology-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.album-container {
  position: relative;
  display: flex;
  align-items: flex-end;
  height: 5.5rem;
}
.album-container .album-tracks {
  display: flex;
  flex-flow: row;
}
.album-container .album-title-container {
  height: 3rem;
  position: absolute;
  display: flex;
  align-items: flex-start;
  top: 2px;
}
.album-container .album-title {
  display: none;
  font-size: 1rem;
  margin: 0;
}
.album-container:hover .album-title {
  display: block;
}

.track-container {
  position: relative;
  display: flex;
  align-items: flex-start;
  height: 4rem;
}
.track-container:hover .track-title-container {
  display: block;
}
.track-container .track-bar {
  display: flex;
  margin: 1.3px;
  height: 2rem;
  width: 4px;
  padding: 0;
  border-radius: 1px;
}
.track-container .track-title-container {
  height: 3rem;
  position: absolute;
  display: flex;
  align-items: flex-start;
  bottom: 1px;
}
.track-container .track-title {
  display: none;
  font-size: 1rem;
  position: absolute;
  top: 1rem;
  margin: 0;
  margin-top: 3px;
  z-index: 10;
  width: 700px;
}
.track-container:hover .track-title {
  display: block;
}

.vertical-chronology-container {
  display: flex;
  flex-direction: column;
  margin: 3rem;
  height: fit-content;
  margin: auto;
  position: relative;
  left: 28vw;
}

.vertical-album-container {
  display: flex;
  flex-direction: column;
  position: relative;
  display: flex;
  align-items: flex-start;
}
.vertical-album-container .album-tracks {
  display: flex;
  flex-flow: row;
}
.vertical-album-container .vertical-album-title-container {
  height: 3rem;
  position: absolute;
  display: flex;
  align-items: flex-start;
  top: 2px;
}
.vertical-album-container .vertical-album-title {
  display: none;
  font-size: 1rem;
  margin: 0;
}
.vertical-album-container:hover .album-title {
  display: block;
}

.vertical-track-container {
  position: relative;
  display: flex;
  align-items: flex-start;
  margin: 2px;
}
.vertical-track-container:hover .track-title-container {
  display: block;
}
.vertical-track-container .vertical-track-bar {
  display: flex;
  margin: 1.3px;
  height: 1rem;
  padding: 0;
  border-radius: 1px;
}
.vertical-track-container .vertical-track-title-container {
  position: absolute;
  display: flex;
  align-items: flex-start;
  bottom: 1px;
}
.vertical-track-container .vertical-track-title {
  font-size: 1rem;
  position: relative;
  top: 0;
  right: 710px;
  text-align: right;
  margin: 0;
  margin-top: 3px;
  z-index: 10;
  width: 700px;
}
.vertical-track-container:hover .vertical-track-title {
  display: block;
}

