.header-container {
  position: fixed;
  left: 0;
  padding: 5px;
  width: 100vw;
  height: 56px;
  display: flex;
  z-index: 10;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-flow: row wrap;
  background-color: #fff;
  border-bottom: pink;
}
@media (max-width: 370px) {
  .header-container {
    height: fit-content;
  }
}

.header-title {
  display: block;
  color: pink;
  margin: 0px;
  font-weight: 800;
  font-size: 30px;
  font-family: "Outfit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

html {
  font-size: 16px;
}

body {
  margin-left: 1rem;
  margin-right: 1rem;
  font-family: -apple-system, "system-ui", "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

ul {
  list-style-type: none;
  /* Removes bullets from an unordered list */
  padding: 0;
}

.whole-container {
  display: flex;
  position: relative;
  top: 56px;
  right: 1rem;
  width: 100vw;
  justify-content: center;
}
@media (min-width: 1200px) {
  .whole-container {
    display: flex;
    justify-content: center;
  }
}

.main-container {
  display: flex;
  flex-flow: column;
  padding-top: 1rem;
  overflow: hidden;
  justify-content: center;
}

.horizontal-chronology {
  display: flex;
  flex-flow: row;
}